/**
 * Authoriztion token
 * TODO: storage - local or session
 * https://github.com/auth0-blog/vue-jwt-authentication
 * https://auth0.com/blog/build-an-app-with-vuejs/
 */

import { tokens } from '../../services'
import { axios, storage } from '../../utils'

const STORAGE_KEY = 'wedn_net_access_token'

/**
 * Initial state
 * @type {Object}
 */
const state = {
  /**
   * è®¿é®ä»¤ç
   * @type {Object}
   */
  token: storage.get(STORAGE_KEY)
}

/**
 * Getters
 * @type {Object}
 */
const getters = {
  /**
   * è·åè®¿é®ä»¤ç
   * @param  {Object} state Vuexç¶æå¯¹è±¡
   * @return {Object}       è®¿é®ä»¤ç
   */
  token: state => state.token
}

/**
 * Mutations
 * @type {Object}
 */
const mutations = {
  /**
   * æ¹åå½åè®¿é®ä»¤ç
   * @param  {Object} state Vuexç¶æå¯¹è±¡
   */
  CHANGE_TOKEN: (state, token) => {
    if (!token) {
      // delete
      delete state.token
      return storage.remove(STORAGE_KEY)
    }
    // change axios authorization header
    axios.defaults.headers.Authorization = `Bearer ${token}`
    state.token = token
    storage.set(STORAGE_KEY, state.token)
  }
}

/**
 * Actions
 * @type {Object}
 */
const actions = {
  /**
   * åå»ºæ°çå®¢æ·ç«¯ä»¤ç
   */
  createToken ({ commit }, { username, password }) {
    return tokens.post({
      username: username.trim(),
      password: password.trim()
    })
    .then(res => {
      commit('CHANGE_TOKEN', res.data.token)
      return res.data.token
    })
  },

  /**
   * æ£æ¥å®¢æ·ç«¯ä»¤çæ¯å¦å¯ç¨
   */
  checkToken ({ commit, getters }) {
    return new Promise((resolve, reject) => {
      // validate local store
      if (!getters.token) {
        return resolve(false)
      }
      // remote
      tokens.get()
        .then(res => resolve(true))
        .catch(err => {
          console.error(err)
          commit('CHANGE_TOKEN')
          resolve(false)
        })
    })
  },

  /**
   * å é¤å®¢æ·ç«¯ä»¤ç
   */
  deleteToken ({ commit }) {
    return tokens.delete()
      .then(res => {
        commit('CHANGE_TOKEN')
      })
  }
}

// change axios authorization header
axios.defaults.headers.Authorization = `Bearer ${state.token}`

// Export module
export default { state, getters, mutations, actions }



